// out: "../style.css"

@import "../vars.less";
@import "../mixins.less";

.form__wrapper {
  .wrapper();
}

.form__slogan {
  margin: 0;
  font-family: 'Oswald', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  color: @secondary-color-text;
}

.form__options-inputs {
  .form-inputs();
 }

.form__options-label {
  .form-text();
}

.form__options-input {
  .form-text();
  .form-input();
}

.form__input-name {
  width: 228px;
  height: inherit;
}

.form__input-weight {
  width: 204px;
  height: inherit;
}

.form__input-age {
  width: 172px;
  height: inherit;
}

.form__tasks-label {
  .form-text();
  color: @fourth-color-text;
  display: flex;
}

.form__contacts {
  position: relative;
  &::before {
    content: "";
    position: absolute;
    width: 99px;
    height: 1px;
    right: 0;
    top: 16px;
    background-color: @color-line;
  }
}

.form__contacts-title {
  .form-title();
  width: 187px;
}

.form__contacts-inputs {
  .form-inputs();
 }

 .form__contacts-label {
  .form-text();
}

.form__contacts-input {
  .form-text();
  .form-input();
}

.form__input-email {
  width: 219px;
  height: inherit;
}

.form__input-phone {
  width: 204px;
  height: inherit;
}

.form__comment-label {
  margin: 0;
  .form-title();
}

.form__comment-text {
  .form-text();
  width: 100%;
  text-align: center;
  border: 0;
  outline: none;
  resize: none;
}

.form__extra-checkbox {
  display: grid;
}

.form__extra-title {
  margin: 0;
  .form-title();
}

.form__extra-label {
  .form-text();
}

.form__button-send {
  width: 100%;
  border: 0;
  outline: none;
  font-family: 'Oswald', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: @tertiary-color-text;
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: #68B738;
}

.form__info {
  margin: 0;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  color: @secondary-color-text;
}

@media(min-width: @tablet) {
  .form__options {
    margin-bottom: 72px;
  }
  .form__wrapper {
    border: 1px solid red;
    .wrapper-tablet();
  }
  .form__slogan {
    .form-text-tablet();
    padding-left: 40px;
    padding-bottom: 66px;
  }
  .form__options-inputs {
    .form-inputs();
    height: 52px;
    padding-left: 40px;
  }
  .form__options-label {
    .form-text-tablet();
  }
  .form__options-input {
    .form-text-tablet();
  }
  .form__input-name {
    width: 567px;
    height: inherit;
  }
  .form__input-weight {
    width: 536px;
    height: inherit;
  }
  .form__input-age {
    width: 497px;
    height: inherit;
  }
  .form__tasks-label {
    .form-text-tablet();
    color: @fourth-color-text;
    padding-left: 40px;
  }
  .form__contacts {
    position: relative;
    &::before {
      content: "";
      position: absolute;
      width: 145px;
      height: 1px;
      right: 0;
      top: 16px;
      background-color: @color-line;
    }
  }
  .form__contacts-title {
    .form-title-tablet();
    width: 512px;
  }
  .form__contacts-inputs {
    .form-inputs();
    height: 52px;
    padding-left: 42px;
   }
   .form__contacts-label {
    .form-text-tablet();
    color: @primary-color-text;
  }
  .form__contacts-input {
    .form-text-tablet();
  }
  .form__input-email {
    width: 551px;
    height: inherit;
  }
  .form__input-phone {
    width: 535px;
    height: inherit;
  }
  .form__comment-label {
    margin: 0;
    .form-title-tablet();
  }
  .form__comment-text {
    .form-text-tablet();
    text-align: left;
  }
  .form__extra-checkbox {
    display: grid;
    grid-template-columns: repeat(auto-fill, 221px);
    column-gap: 123px;
    padding-left: 40px;
  }
  .form__extra-title {
    margin: 0;
    .form-title-tablet();
  }
  .form__extra-label {
    .form-text-tablet();
    width: 221px;
    margin-right: 100px;
  }
  .form__button-send {
    width: 282px;
    font-size: 20px;
    padding-top: 16px;
    padding-bottom: 16px;
    background-color: @button-bg-color;
  }
  .form__info {
    font-size: 16px;
    line-height: 16px;
  }
}

@media(min-width: @desktop) {
  .page-header__form {
    &::before {
      display: none;
    }
  }
  .nav-item__link-form{
    color: @secondary-color-text;
  }
  .nav-item__link--active-form {
    &::before {
      background-color: @nav-link-active;
    }
  }
  .form__wrapper {
    .wrapper-desktop();
  }
  .form__slogan {
    .form-text-desktop();
    padding-left: 0;
  }
  .form__wrapper-original {
    display: grid;
    grid-template-columns: repeat(auto-fill, 570px);
  }
  .form__options-inputs {
    padding-left: 0;
  }
  .form__tasks-label {
    padding-left: 0;
  }
  .form__wrapper-contacts {
    display: flex;
  }
  .form__contacts-label {
    width: 85px;
  }
  .form__contacts-inputs {
    width: 486px;
    padding-left: 0;
  }
  .form__extra-checkbox {
    column-gap: 104px;
    padding-left: 0;
  }
}
